.Vlt-card {
	background: $white;
	border-radius: $unit1;
	box-shadow: 0 4px 20px rgba($black, 0.1);
	display: block;
	margin-bottom: $unit3;
	padding: $unit3;
	position: relative;
	width: 100%;

	&--border {
		box-shadow: inset 0 0 0 1px $grey-light;
	}

	&[class*='Vlt-card--border-top-'] {
		overflow: hidden;
		padding: 23px $unit3 $unit2;

		&::before {
			border-top-left-radius: $unit1;
			border-top-right-radius: $unit1;
			content: '';
			height: 7px;
			left: 0;
			position: absolute;
			top: 0px;
			width: 100%;
		}
	}

	&--border-top {
		&-blue {
			&::before {
				background-color: $blue;
			}
		}

		&-green {
			&::before {
				background-color: $green;
			}
		}

		&-indigo {
			&::before {
				background-color: $indigo;
			}
		}

		&-orange {
			&::before {
				background-color: $orange;
			}
		}

		&-purple {
			&::before {
				background-color: $purple;
			}
		}

		&-red {
			&::before {
				background-color: $red;
			}
		}

		&-teal {
			&::before {
				background-color: $teal;
			}
		}

		&-yellow {
			&::before {
				background-color: $yellow;
			}
		}
	}

	&--gradient-wrapper {
		border-radius: $unit1;
		margin-bottom: $unit3;
		padding: 1px;
		position: relative;
		width: 100%;

		.Vlt-card {
			box-shadow: inset 0 0 0 0;
			margin-bottom: 0;
		}
	}

	&--plain {
		box-shadow: none;
	}

	&--lesspadding {
		padding: $unit2;

		.Vlt-card__footer,
		.Vlt-card__header {
			margin-left: 0;
			margin-right: 0;
		}

		.Vlt-card__header {
			padding-bottom: $unit1;
		}

		.Vlt-card__footer {
			padding-bottom: 2px;
			padding-top: $unit1;
		}

		.Vlt-card__corner {
			right: 16px;
			top: 16px;
		}

		.Vlt-card__image {
			margin: -#{$unit2} -#{$unit2 + 1px} 19px;
		}

		&.Vlt-card--scrollable .Vlt-card__content {
			padding-bottom: $unit1;
			padding-top: $unit1;
		}
	}

	&__image {
		align-items: center;
		background: $grey;
		border-top-left-radius: $unit1;
		border-top-right-radius: $unit1;
		color: $white;
		display: flex;
		flex-flow: column;
		height: 90px;
		justify-content: center;
		margin: -#{$unit3} -#{$unit3 + 1px} $unit3;
		position: relative;
		text-align: center;

		svg {
			fill: $white;
		}

		* {
			max-height: 100%;
			z-index: 1;
		}

		&--app,
		&--tech,
		&--grid {
			&:before {
				content: '';
				display: block;
				height: 100%;
				left: 0;
				opacity: 0.1;
				position: absolute;
				top: 0;
				width: 100%;
				z-index: 1;
			}
		}

