		&__icon {
			background: $white;
			border-radius: $unit0;
			box-shadow: 0 4px 4px 0 rgba($black, 0.14);
			height: 60px;
			width: 60px;

			svg,
			img {
				height: 40px;
				margin: 10px;
				width: 40px;
			}
		}

		&--blue {
			background: linear-gradient(to right, lighten($blue-dark, 5), darken($blue-dark, 10));

			.Vlt-card__image__icon svg {
				fill: $blue-dark;
			}
		}

		&--blue-light {
			background: linear-gradient(to right, lighten($blue, 5), darken($blue, 10));

			.Vlt-card__image__icon svg {
				fill: $blue;
			}
		}

		&--red {
			background: linear-gradient(to right, lighten($red, 5), darken($red, 10));

			.Vlt-card__image__icon svg {
				fill: $red;
			}
		}

		&--orange {
			background: linear-gradient(to right, lighten($orange, 10), darken($orange, 5));

			.Vlt-card__image__icon svg {
				fill: $orange;
			}
		}

		&--indigo {
			background: linear-gradient(to right, lighten($indigo, 5), darken($indigo, 10));

			.Vlt-card__image__icon svg {
				fill: $indigo;
			}
		}

		&--green {
			background: linear-gradient(to right, lighten($green, 5), darken($green, 10));

			.Vlt-card__image__icon svg {
				fill: $green;
			}
		}

		&--purple {
			background: linear-gradient(to right, lighten($purple, 5), darken($purple, 10));

			.Vlt-card__image__icon svg {
				fill: $purple;
			}
		}

		&--teal {
			background: linear-gradient(to right, lighten($teal, 5), darken($teal, 10));

			.Vlt-card__image__icon svg {
				fill: $teal;
			}
		}
	}

	&__header {
		padding-bottom: #{$unit2};

		.Vlt-btn,
		.Vlt-dropdown__btn,
		h1,
		h2,
		h3,
		h4,
		h5 {
			margin: 0 !important;
		}
	}

	&__footer {
		border-top: 1px solid rgba($black, 0.1);
		margin: #{$unit2} -#{$unit1} -#{$unit1};
		padding-top: $unit2;

		.Vlt-btn {
			margin-bottom: 0;
			margin-top: 0;
		}

		&--short {
			margin-bottom: -12px;
			margin-top: $unit2;
			padding-top: $unit1;
		}

		&--noborder {
			border: 0;
			margin-top: 0;
		}
	}

	&--scrollable {
		display: flex;
		flex-flow: column;

		.Vlt-card__footer {
			flex-shrink: 0;
			margin-top: 0;
		}

		.Vlt-card__header,
		.Vlt-card__image {
			flex-shrink: 0;
			margin-bottom: 0;

			& + .Vlt-card__content {
				padding-top: $unit2;
			}
		}

		.Vlt-card__content {
			flex: auto; // Has to be 'auto' for IE
			overflow-y: auto;
			padding-bottom: $unit2;
		}
	}

	&--stretch {
		height: 100%;
	}

	&__cta {
		margin-bottom: -$unit1;
		text-align: center;

		.Vlt-btn {
			@extend %Vlt-btn--app;
		}
	}

	&__content {
		.Vlt-icon--larger:first-child,
		.Vlt-icon--largest:first-child {
			margin-bottom: $unit3;
		}
	}

	&__corner {
		font-size: 1.1rem;
		font-weight: 600;
		line-height: 1.8rem;
		padding-right: 23px;
		position: absolute;
		right: 24px;
		top: 24px;

		> svg {
			height: 18px;
			position: absolute;
			right: 0;
			top: 0;
			width: 18px;
		}

		.Vlt-btn {
			margin: 0;
			margin-right: -23px;
		}
	}
}

a.Vlt-card,
.Vlt-card--clickable {
	cursor: pointer;
	transition: transform 0.1s;

	&:hover {
		transform: scale(1.022);
    }

    &:active {
    	transform: scale(1);
    }
}
